.form-selection {
  display: grid;
  row-gap: 23px;
}

.form-selection__wrapper {
  .wrapper();
}

.form-selection__selection-task {
  display: grid;
}

.form-selection__contact {
  background-color: @white;
}

.form-selection__comment {
  display: flex;
  flex-direction: column;
}

.form-selection__additionally {
  display: grid;
  row-gap: 34px;
}

.form-selection__additionally--notation {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  text-align: center;
}
